var html = require('choo/html')
var choo = require('choo')

var app = choo()                
const getBase = function() {
//  return 
  return "/choo-examples"
}
app.route(getBase(), view)             
app.route(getBase()+'/second', second)      
app.mount('body')                  

function view (state, emit) {
  return html`
    <body>
      <h2>Hello World</h2>
      <a href="#second">
        Navigate to the next route.
      </a>
    </body>
  `
}
function second (state) {
  return html`
    <body>
      <a href="${getBase()}">
        Navigate back, ${state.title}.
      </a>
    </body>
  `
}
